@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap);
:root {
    --primaryColor: #5b83c3;
    --black: #333333;
    --grey: #c4c4c4;
    --red: #c0392b;
    --green: #53953b;
}

* {
    font-family: "Montserrat", sans-serif;
}

body {
    margin: 0;
    font-family: "Montserrat", sans-serif;
    font-size: 0.9rem;
}
.App {
    display: flex;
    min-height: 100vh;
    background-color: #ffffff;
    flex-direction: column;
    padding: 10px;
}

.App .main-container {
    width: 375px;
    height: 600px;
    padding: 10px;
    border: 1px solid #e6e6e6;
    margin: auto auto;
    border-radius: 10px;
}

@media screen and (max-width: 375px) {
    .App .main-container {
        border: none;
        margin: 5px;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
}
.home-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
}

.home-container .search-input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.home-container .icons {
    height: auto;
    width: 40px;
}
.top-crypto-asset-container .title {
    font-weight: 700;
}

.top-crypto-asset-container .top-crypto-list {
    margin: 10px;
    -ms-overflow-style: none;
    scrollbar-width: none;
    height: 75vh;
    overflow-y: scroll;
    padding-right: 17px;
    box-sizing: content-box;
}

.top-crypto-asset-container .loader-position {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.top-crypto-asset-container .no-stock-found {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 100px 0;
    font-weight: 600;
}

.top-crypto-asset-container .top-crypto-list::-webkit-scrollbar {
    display: none;
}
.searched-stock-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
    padding: 10px;
    border-bottom: 1px dotted black;
}

.searched-stock-container .stock-name,
.searched-stock-container .bookmark-icon {
    cursor: pointer;
}

.searched-stock-container .bookmark-icon {
    height: 20px;
}
.watch-list-container {
    align-items: center;
}

.watch-list-container .title {
    display: flex;
    justify-content: space-between;
    font-weight: 700;
    margin: 10px 0;
    padding: 0 10px;
}

.watch-list-container .icons {
    height: 20px;
}

.watch-list-container .watch-list-stock {
    -ms-overflow-style: none;
    scrollbar-width: none;
    height: 70vh;
    overflow-y: scroll;
    box-sizing: content-box;
}

.watch-list-container .watch-list-label {
    font-weight: 700;
}

.watch-list-container .no-stock-found {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 20px 0;
    font-weight: 600;
}

.watch-list-container .watch-list-stock::-webkit-scrollbar {
    display: none;
}

@media screen and (max-width: 375px) {
    .watch-list-container .watch-list-stock {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
}
.stock-change-info-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.stock-change-info-container .stock-change-detail {
    width: 95%;
    display: flex;
    border: 1px solid #e6e6e6;
    justify-content: space-around;
    align-items: center;
    margin: 10px 0;
    border-radius: 5px;
    padding: 10px 0;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.stock-change-info-container .stock-change-detail .graph-icon {
    height: 40px;
}

.stock-change-info-container .delete-icon {
    height: 20px;
}
.info-record {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0;
}

.info-record .name-detail {
    display: flex;
    align-items: center;
}

.info-record .name {
    font-weight: 700;
}

.info-record .bookmark-icons {
    height: 20px;
    margin: 0 0 0 5px;
}

.info-record .price-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.info-record .price-detail .price {
    font-weight: 700;
    margin-bottom: 2px;
}

.stock-detail-container .loader-position {
    padding: 250px 150px;
}

.stock-detail-container .graph {
    height: 200px;
    border: 1px solid #e6e6e6;
    border-radius: 10px;
    margin: 10px 0;
}

.stock-detail-container .graph-input {
    display: flex;
    justify-content: space-evenly;
    margin: 10px 0;
    font-weight: 600;
}

.stock-detail-container .graph-input .duration-selected,
.stock-detail-container .graph-input .duration-not-selected {
    padding: 5px;
    width: 40px;
    border-radius: 5px;
    color: #fff;
    text-align: center;
    background-color: var(--primaryColor);
    box-shadow: -5px 0 10px #bdb4b4;
}

.stock-detail-container .graph-input .duration-selected {
    color: #fff;
    background-color: var(--primaryColor);
}

.stock-detail-container .graph-input .duration-not-selected {
    color: #000;
    background-color: #fff;
}

.stock-detail-container .news-section {
    padding: 20px 10px 5px 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 1px solid #e6e6e6;
    border-radius: 10px;
    -ms-overflow-style: none;
    scrollbar-width: none;
    height: fit-content;
    overflow-y: scroll;
}

.stock-detail-container .news-section .title {
    font-weight: 700;
    padding-bottom: 10px;
}

.stock-detail-container .news-list {
    -ms-overflow-style: none;
    scrollbar-width: none;
    height: 35vh;
    overflow-y: scroll;
    box-sizing: content-box;
}

.stock-detail-container .news-list .no-news-found {
    text-align: center;
    font-weight: 600;
    padding: 20px 0;
}

.stock-detail-container .news-list::-webkit-scrollbar {
    display: none;
}
.news-detail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin: 10px 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 1px solid #e6e6e6;
    border-radius: 10px;
}

.news-detail .headline {
    font-size: 1.1rem;
    font-weight: 700;
    margin-bottom: 10px;
}

.news-detail .content {
    font-size: 0.8rem;
}

.news-detail .news-icons {
    height: auto;
    width: 80px;
}
